.preloader-backdrop {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 13000;
  visibility: hidden;
  -webkit-transition-duration: 400ms;
  transition-duration: 400ms;
  visibility: visible;
  opacity: 0;
  background: none;
}
.preloader-backdrop.not-animated {
